<template>
  <div>
    <!-- header -->
    <div class="top" >
        <div class="topmiddle" id="mytop1">
            <div class="container" id="topcenter">
                <div class="row">
                    <div class="col-md-7 offset-md-1"><router-link to="/"></router-link></div>
                    <div class="col-md-1 "><router-link to="/visualization"></router-link></div>
                    <div class="col-md-1 "><router-link to="/mypage"></router-link></div>
                    <div class="col-md-1 "><router-link to="/login"></router-link></div>
                </div>
            </div>
        </div>
        <hr>
    </div>
    <!--  -->
    <div class="container" id="write">
      <div class="row" id="d1">
        <h1 class=""></h1>
        <form @submit.prevent="login">
          <div class="form-group">
            <label for="exampleInputText1"></label>
            <input type="text" class="form-control" id="exampleInputText1" v-model="title">
          </div>
          <div class="form-group">
            <label for="exampleFormControlTextarea1" ></label>
            <textarea class="form-control" id="exampleFormControlTextarea1" rows="2" v-model="summary"></textarea>
          </div>
          <div class="form-group">
            <label for="markdown-editor"></label>
            <!-- <textarea class="form-control" id="exampleFormControlTextarea2" rows="3" v-model="text"></textarea> -->
            <textarea class="form-control" id="markdown-editor " rows="3" v-model="text"></textarea>
            <mavon-editor v-model="text" :toolbars="toolbars" />
          </div>
          <!-- //  -->
          <div class="form-group mt-2">
            <div class=" mb-3">
              <div class="d-flex align-items-center ">
                <div class="p-2"> :</div>
                <div class="kuang p-2 d-flex align-items-center border border-dark rounded mx-3 my-3" id="div1">
                  <div v-if="selectedTags.length>0" id="cd1" title="">
                    <div class="custom-tag bg-primary text-white mx-1 my-1" id="sp1" v-for="(tag, index) in selectedTags" :key="index">
                      <span >{{tag}}</span>
                      <button class="close-btn text-white" id="cb1" @click="removeTag(index)"></button>
                    </div>
                  </div>
                  <div v-else id="cd1" title=""></div>
                  <div class="flex-grow-1">
                    <input class="form-control border-0 " id="i1" list="tagsList" type="text" placeholder="Enter" @keydown.enter.prevent="add"> 
                  </div>
                  <div class="float-right">
                    <p class="font-weight-normal m-0 ml-2">{{ 10-selectedTags.length }}</p>
                  </div>
                  
                </div>
              </div>
              <div class="biaoqianlan">
                <span class="btn btn-outline-primary border border-primary my-2 mx-2 " v-for="tag in tags" :key="tag.classification" @click="addTag(tag.title)">{{ tag.title }}</span>
                <datalist id="tagsList">
                  <option v-for="tag in tags" :key="tag.classification" :value="tag.title">{{ tag.title }}</option>
                </datalist>
              </div>
            </div>
          </div>
          <!-- //  -->
          <div class="offset-md-3 mb-2">
            <button type="submit" class="btn btn-primary me-4" ></button>
            <button @click="$router.back()" class="btn btn-primary offset-md-2"></button>
          </div>
          
        </form>
      </div>
    </div>
    

  </div>
</template>

<script>
export default {
  data() {
    return {
      inputText: '',
      //  tags
      tags: {}, // 
      selectedTags: [],
      // form 
      title: '',
      summary: '',
      text: '',
      // mypage 
      username: '',
      // classification: ''
      context: ' ',//
      toolbars: {
          bold: true, // 
          italic: true, // 
          header: true, // 
          underline: true, // 
          mark: true, // 
          superscript: true, // 
          quote: true, // 
          ol: true, // 
          link: true, // 
          imagelink: true, // 
          help: true, // 
          code: true, // code
          subfield: true, // 
          fullscreen: true, // 
          readmodel: true, // 
          undo: true, // 
          trash: true, // 
          save: true, // eventssave
          navigation: true // 
      }
    }
  },
  methods: {
    // 
    async getCategory() {
      const { data: res } = await this.$http.get('/category/detail/')
      this.tags = res.data
      // []
      // this.tags = this.tags.map(item => item.title)
      console.log('123', this.tags)
      },
    async postInfo() {
      const data = {title: this.title,
          summary: this.summary,
          text: this.text, username: this.username,
          classification: JSON.stringify(this.selectedTags)}
      await this.$http.post('/bloginfo/', 
        this.$qs.stringify(data)
      )},
    async login() {
      await this.postInfo()
      this.title = '',
      this.summary = '',
      this.text = '',
      // mypage 
      // classification: ''
      this.selectedTags = [],
      this.$router.go(0);
    },
    // 
    removeTag(index) {
      this.selectedTags.splice(index, 1);
    },
    // spaninput
    addTag(t) {
      if(this.selectedTags.length < 10){
        this.selectedTags.push(t)
        console.log(this.selectedTags)
      }else{
         // 
        console.log("");
      }
    },
    // select
    add(e) {
      // input10
      if (this.selectedTags.length < 10) {
        console.log(e.target.value)
        this.selectedTags.push(e.target.value)
        console.log(this.selectedTags)
        this.clearInput()  
      }else{
        e.preventDefault(); // 
      }

    },
    // input
    clearInput() {
      document.getElementById('i1').value = ''; 
    }

  },
  created() {
    this.username = localStorage.getItem('username')
    this.getCategory()
  },
  mounted() {
    //  <mavon-editor>  <textarea> 
  //   this.$nextTick(() => {
  //     this.$refs.editor.textarea = document.getElementById('markdown-editor')
  //   })
  }

}
</script>

<style lang="less" scoped>
#write{
        height: 100%;
        // margin: 0;
        display: grid;
        place-items: center; /*  */
        #d1 {
          height: 100%;
          width: 80%;
          border: 1px solid black;
          background-color:antiquewhite
        }
        form {
          div {
            span {
              margin: 2px, 2px, 2px, 2px;
            }
          }
        }
        #div1{
        width: 90%;

        #i1:focus {
          outline: none; /*  */
          border: none; /*  */
        }
        #sp1 {
          display: inline-flex;
          align-items: center;
          border: 0px solid #ccc;
          padding: 0.3rem 0.5rem;
          border-radius: 4px;
          margin-right: 0.5rem;
          
        }

        #sp1 span {
          margin-right: 0.5rem;
        }

        #cb1 {
          background-color: transparent;
          border: none;
          cursor: pointer;
          font-size: 12px;
          font-weight: bold;
          color: #666;
        }

        .close-btn:hover {
          color: #f00; /*  */
        }
      }
    }

</style>